<template>
  <div id="shopCart">
    <h4>Danh Sách Đã Chọn Mua</h4>
    <h5 v-show="listProductBought.length <=0">Chưa có sản phẩm nào được thêm</h5>
    <table v-show="listProductBought.length > 0">
      <thead>
        <tr>
          <th>STT</th>
          <th>Tên Sản Phẩm</th>
          <th>Giá Tiền</th>
          <th>Tác Vụ</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(product, index) in listProductBought" :key="index">
          <th>{{index + 1}}</th>
          <td>{{product.name}}</td>
          <td>{{product.price | formatMoney(dotOrComma)}}</td>
          <td>
            <button @click="onDelete(product)">Xoa</button>
          </td>
        </tr>
        <tr>
          <td>
            <span v-show="listProductBought.length > 0">Tong tien: {{total | formatMoney(dotOrComma)}}</span>
          </td>
        </tr>
    </tbody>
    </table>
  </div>
</template>
<script>
export default {
  name: "ShoppingCart",
  components: {},
  props: ["listProductBought", "DeleteProduct", "dotOrComma"],
  data(){
    return{
      total: 0,
      usd: 0
    };
  },
  methods: {
    onDelete() {
      this.DeleteProduct();
    }
  },
  watch: {
    listProductBought: function() {
      let total = 0;
      this.listProductBought.forEach(product => {
        total += product.price;
      });
      this.total = total;
    },
  }
}
</script>

