<template>
  <div id="productPage">
    <h4>Danh Sách Sản Phẩm</h4>
    <div>
      <input type="radio"
      id="daucham"
      name="hienthi"
      value="."
      v-model="dotOrComma">
      <label for="daucham">Dấu Chấm</label>
    </div>
    <div>
      <input type="radio"
      id="dauphay"
      name="hienthi"
      value=","
      checked
      v-model="dotOrComma">
      <label for="dauphay">Dấu phẩy</label>
    </div>
    <table>
      <thead>
        <tr>
          <th>STT</th>
          <th>Tên Sản Phẩm</th>
          <th>Giá Tiền</th>
          <th>Tác Vụ</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(product, index) in products" :key="index">
          <th>{{index + 1}}</th>
          <td>{{product.name}}</td>
          <td>{{product.price  | formatMoney(dotOrComma)}}</td>
          <td>
            <button @click="ClickBuyProduct(product)">Mua</button>
          </td>
        </tr>
      </tbody>
    </table>
    <hr>
    <ShoppingCart
    :listProductBought="listProductBought"
    :DeleteProduct="(product)=> DeleteProduct(product)"
    :dotOrComma="dotOrComma"
    ></ShoppingCart>
  </div>
</template>
<script>
import ShoppingCart from "./ShoppingCart.vue";
export default {
  name: "ProductPage",
  components: {
    ShoppingCart
  },
  props: ["products"],
  data(){
    return {
      dotOrComma: ",",
      listProductBought: []
    }
  },
  methods: {
    ClickBuyProduct(product){
      this.listProductBought.unshift(product);
      alert("Bạn vừa thêm một sản phẩm mới.")
      return this.listProductBought;
    },
    DeleteProduct(product){
      var isDegree = confirm("Bạn có muốn xóa sản phẩm này không!");
      if(!isDegree) return;
      this.listProductBought.splice(product, 1);
    }
  }
}
</script>
